@import url(https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap);
body {
  min-width: 1440px;
  margin: 0;
  font-family: Rubik, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

header {
    background-color: Black;
    height: 80px;
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: center;
}

.header-container {
    display: flex;
    min-width: 1440px;
    justify-content: space-around;
}

.header-logo {
    display: flex;
    align-items: center;
}

.header-logo img {
    Width: 120px;
    Height: 35px;
}

.header-links {
    display: flex;
}

.header-links a {
    font-family: Rubik;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: left;
    padding: 24px 20px 24px 20px;
    font-style: normal;
    display: flex;
    align-items: center;
    color: #FFFFFF;

}

.basket-logo {
    display: flex;
    align-items: center;
}

.basket-logo img {
    width: 40px;
    height: 37px;
}


section {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: center;
    background-image: url(/willskilltest2/static/media/yellow-background.d903ab16.png);
    /*z-index: -2;*/
}

.yellow-section-container {
    min-width: 1440px;
    max-width: 1440px;
    /*overflow: hidden;*/
}

.yellow-section-content {
    display: flex;
    position: relative;
}

.yellow-text {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.yellow-pictures img {
    position: absolute;
    right: -2%;
    top: 80px;
    /*z-index: -1;*/
}

.yellow-text {
    display: flex;
    text-align: start;
    flex-direction: column;
    padding-left: 64px;
}

.yellow-main-text {
    padding: 96px 0 120px 0;
}

.yellow-main-text h1 {
    font-family: Rubik;
    font-style: normal;
    font-weight: 700;
    font-size: 144px;
    line-height: 152px;
    text-transform: uppercase;
    color: #FFFFFF;
    padding: 0;
    margin: 0;
    text-align: start;
}

.yellow-main-text h2 {
    font-family: 'Rubik';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 28px;
    color: #000000;
    text-align: start;
    padding-top: 24px;
}

.yellow-additional-text {
    padding-bottom: 96px;
}

.yellow-additional-text h2 {
    font-family: Rubik;
    font-style: normal;
    font-weight: 700;
    font-size: 36px;
    line-height: 43px;
    text-transform: uppercase;
    color: #000000;
    margin: 0;
    padding: 0;
}

.yellow-additional-text h3 {
    font-family: Rubik;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    color: #000000;

}


.final-section {
    background: #111111;
    min-height: 962px;
    display: flex;
    width: 100%;
    justify-content: center;
}

.final-section-container {
    min-width: 1440px;
}

.final-section-title {
    padding: 110px 64px 85px 64px;
}

.final-section-title h1 {
    font-family: Rubik;
    font-style: normal;
    font-weight: 700;
    font-size: 48px;
    line-height: 57px;
    color: #FFFFFF;
    text-transform: uppercase;
}

.final-section-title span {
    color: Orange;
}

.prices {
    display: flex;
    justify-content: center;
    align-items: center;
}

.before h2 {
    font-family: Rubik;
    font-style: normal;
    font-weight: 400;
    font-size: 36px;
    line-height: 43px;
    color: #ADADAD;
    padding-right: 16px;
    text-decoration: line-through;
}

.now h2 {
    font-family: Rubik;
    font-style: normal;
    font-weight: 400;
    font-size: 48px;
    line-height: 57px;
    color: #FFFFFF;
    padding-left: 16px;
}

.final-section-pictures-block {
    display: flex;
    padding-top: 50px;
    justify-content: space-evenly;
}

.ginger-img img {
    width: 193px;
    height: 168px;
}

.ginger-img {
    padding-bottom: 45px;
}

.ginger-text h3 {
    font-family: Rubik;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    color: #FFFFFF;
    margin: 0;
}

.ginger-text span {
    color: rgba(252, 181, 0, 1);
}

.bottle-img img {
    width: 480px;
    height: 160px;
}

.bottle-img {
    padding-bottom: 50px;
}

.bottle-text h2 {
    font-family: Rubik;
    font-style: normal;
    font-weight: 400;
    font-size: 36px;
    line-height: 43px;
    color: #FFFFFF;
    margin: 0;
}

.bottle-text h3 {
    font-family: Rubik;
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 28px;
    color: #FCB500;
    margin: 0;
}

.virus-img img {
    width: 192px;
    height: 160px;
}

.virus-img {
    padding-bottom: 50px;
}

.virus-text h3 {
    font-family: Rubik;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    margin: 0;
    color: #FFFFFF;
}

.virus-text span {
    color: #FCB500;
}

.order-btn {
    display: flex;
    justify-content: center;
    width: 100%;
    padding-top: 64px;
    padding-bottom: 112px;
}

.order-btn button {
    background: #FCB500;
    border-radius: 7px;
    width: 256px;
    height: 56px;
    font-family: Rubik;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;

}
